import angular from 'angular';

// Module dependencies
import config from '../config';

class Campaign {
  constructor($http, $q, config) {
    Object.assign(this, { $http, $q });

    this.baseUrl = `${config.serverUrl}/campaigns`;
  }

  /**
   * Find campaigns by params
   *
   * @param {object} [params={}]
   *
   * @returns {HttpPromise}
   */
  find(params = {}) {
    return this.$http.get(this.baseUrl, { params });
  }

  /**
   * Find a single campaign by params
   *
   * @param {object} params
   *
   * @returns {Promise}
   */
  findOne(params) {
    return this.$q((resolve, reject) => {
      this.find(params)
        .then(response => {
          if (response.data.length === 1) {
            resolve(response.data[0]);
          } else {
            reject(new Error('Campaign not found'));
          }
        })
        .catch(() => reject(new Error('Failed connecting to server')));
    });
  }

  /**
   * Save a new or existing campaign
   *
   * @param {object} campaign
   *
   * @returns {HttpPromise}
   */
  save(campaign) {
    if (_.has(campaign, '_id')) {
      return this.$http.put(`${this.baseUrl}/${campaign._id}`, campaign);
    } else {
      return this.$http.post(this.baseUrl, campaign);
    }
  }

  /**
   * Set a deed as published/unpublished for a campaign
   * TODO: should we use JSON patch?
   *
   * @param {object}  campaign
   * @param {object}  deed
   * @param {boolean} [published=true]
   */
  setPublished(campaign, deed, published = true) {
    let action = published ? 'post' : 'delete';

    return this.$http[action](`${this.baseUrl}/${campaign._id}/deeds/${deed._id}/published`);
  }
}

Campaign.$inject = ['$http', '$q', 'config'];

export default angular.module('app.services.Campaign', [config])
  .service('Campaign', Campaign)
  .name;
